.navbar input[type="checkbox"],
.navbar .hamburger_lines {
  display: none;
}

.navbar {
  display: flex;
  text-align: center;
  background: white;
  position: fixed;
  top: 0;
  width: 100%;
  opacity: 0.85;
  height: 70px;
  z-index: 10;
}

.navbar_container {
  padding-block: 12px;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-inline: 5%;
  margin-inline: 5%;
}

.menu_items {
  order: 2;
  display: flex;
}

.menu_items li {
  list-style: none;
  margin-left: 1.5rem;
  margin-bottom: 0.5rem;
  font-size: 1.3rem;
  margin-bottom: 0;
}

.menu_items a {
  text-decoration: none;
  color: #283c59;
  font-weight: 600;
  transition: color 0.3s ease-in-out;
}

.menu_items a:hover {
  color: rgb(184, 184, 184);
  transition: color 0.3s ease-in-out;
}

@media (max-width: 768px) {
  .navbar {
    width: 100%;
    opacity: 0.95;
  }

  .navbar_container input[type="checkbox"],
  .navbar_container .hamburger_lines {
    cursor: pointer;
    display: block;
  }

  .navbar_container {
    display: block;
    position: relative;
  }

  // checkbox & hamberger position setting
  .navbar_container input[type="checkbox"] {
    position: absolute;
    display: block;
    height: 32px;
    width: 30px;
    z-index: 5;
    opacity: 0;
    top: 16px;
  }

  .navbar_container .hamburger_lines {
    display: block;
    position: absolute;
    top: 24px;
    left: 37px;
    height: 23px;
    width: 30px;
    z-index: 1;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }

  .navbar_container .hamburger_lines .line {
    display: block;
    height: 3px;
    width: 30px;
    border-radius: 10px;
    background: #283c59;
  }

  .navbar_container .hamburger_lines .line1 {
    transform-origin: 0% 50%;
    transition: transform 0.4s ease-in-out;
  }

  .navbar_container .hamburger_lines .line2 {
    transition: transform 0.2s ease-in-out;
  }

  .navbar_container .hamburger_lines .line3 {
    transform-origin: 0% 100%;
    transition: transform 0.4s ease-in-out;
  }

  // side popup box
  .navbar .menu_items {
    background: white;
    width: 140%;
    display: flex;
    flex-direction: column;
    margin-top: 58px;
    margin-left: -20%;
    padding-left: 0%;
    padding-block: 20%;
    transition: transform 0.5s ease-in-out;
    transform: translate(-120%);
    box-shadow: 5px 0px 1px 0px #aaa;
  }

  .navbar .menu_items li {
    margin-bottom: 1.5rem;
    font-size: 1.3rem;
    font-weight: 500;
    margin-left: 0px;
  }

  .logo {
    width: calc(100% - 100px);
    display: flex;
    justify-content: flex-end;
    position: absolute;
    top: 14px;
  }
  .hide_text {
    display: none;
  }

  .navbar_container input[type="checkbox"]:checked ~ .menu_items {
    transform: translateX(0);
  }

  .navbar_container input[type="checkbox"]:checked ~ .hamburger_lines .line1 {
    transform: rotate(45deg);
    width: 29.3px;
  }

  .navbar_container input[type="checkbox"]:checked ~ .hamburger_lines .line2 {
    transform: scaleY(0);
  }

  .navbar_container input[type="checkbox"]:checked ~ .hamburger_lines .line3 {
    transform: rotate(-45deg);
    width: 29.3px;
  }
}