.gradient_text {
  background: linear-gradient(to right, #3e84d4, #74a0e2);
  background-clip: text;
  -webkit-text-fill-color: transparent;
}

@media (max-width: 768px) {
  .footer_inner {
    flex-direction: column;
    padding-block: 50px !important;
    max-width: 1100px;
    
  }
  .footer_inner_title {
    width: 100%;
  }
  .footer_inner_info {
    width: 100%;
    margin-top: 35px;
  }
  .footer_inner_info > p {
    display: block;
    align-items: center;
    text-align: left !important;
    padding-inline: 20px;
  }
  .footer_inner > div {
    padding-block: 20px;
    position: relative;
    margin: 0 auto;
    max-width: 1100px;
    height: 180px;
  }
}


.footer_inner_info > p {
  margin: 0;
  text-align: right;
}
.footer_inner_info {
  margin-left: auto;
  color: gray
}
.footer_inner_title {
  display: flex;
  align-items: center;
}

.footer_inner_title > a {
  font-size: 2em;
  font-weight: 600;
  padding-left: 10px;
}

.footer_inner_title > img {
  margin-left: 16px;
  width: 40px
}

.footer_inner {
  display: flex;
  align-items: center;
  padding-block: 16px;
  position: relative;
  margin: 0 auto;
  max-width: 1100px;
  height: 180px;
}


.notice_item {
  line-height: 30px;
  font-size: 1.1em;
  padding-right: 20px;
}

.footer_section_list {
  margin-block: 2em;
  padding-top: 35px; 
  padding-bottom: 45px;
  word-break: normal;
  margin: 0;
}

.footer_section_inner > p {
  font-size: 2em;
  font-weight: 600;
  padding: 16px;
  margin: 0;
}

.footer_section_inner {
  padding-block: 40px;
  position: relative;
  margin: 0 auto;
  max-width: 1100px;
}

.footer_section_notice1 {
  width: 100%;
  border-bottom: 1px solid #e6e6e6;
}
.footer_section_notice2 {
  width: 100%;
}

.footer {
  width: 100%;
  background-color: #303131;
  color: white;
}
