.introSmallText {
  margin-top: 20px;
  font-size: 1.1rem;
  color: #283C59;
  text-align: center;
}

.introBigText {
  font-size: 2rem;
  color: #283C59;
  text-align: center;
  font-weight: bold;
}


.introductionContainer {
  margin: 50px 5% 0 5%;
  display: flex;
  justify-content: center;
}





// 이미지 3d css
.body {
  height: auto;
  margin-top: 100px;
  display: grid;
  margin-left: 100px;
  margin-right: 100px;
}

.tariffCards > div {
  width: 25%;
  height: 250px;
  border-radius: 10%;
  transition: all 0.4s ease-in-out;
  overflow: hidden;
  cursor: pointer;
}
.tariffCards {
  top: 50%;
  left: 50%;
  bottom: 0;
  user-select: none;
  transform: translate3d(0,0,);
  width: 100%;
  height: 100%;
}

.tariffCards > div img {
  pointer-events: none;
  width: 100%;
  height: 100%;
}

.tariffCards > div h3 {
  position: absolute;
  bottom: 28px;
  left: 15px;
  font-size: 18px;
  font-weight: 800;
  z-index: 66;
}

.tariffCards > div.first {
  margin-top: 0;
  z-index: 1;
  transition: all 0.4s ease-in-out;
  transform: perspective(800px) rotateY(30deg);
}

.tariffCards > .first:hover {
  position: relative;
  z-index: 99999;
  transform: scale(1.2) translate(128%, -150px);
  transition: all 0.4s ease-in-out;
}

.tariffCards > div.second {
  margin-top: -250px;
  margin-left: 19.5%;
  z-index: 2;
  transition: all 0.4s ease-in-out;
  transform: perspective(800px) rotateY(30deg);
}

.tariffCards > .second:hover {
  position: relative;
  z-index: 99999;
  transform: scale(1.2) translate(63%, -150px);
  transition: all 0.4s ease-in-out;
}

.tariffCards > div.third {
  margin-top: -250px;
  margin-left: 39%;
  z-index: 3;
  transition: all 0.4s ease-in-out;
  transform: perspective(800px) rotateY(30deg);
}

.tariffCards > .third:hover {
  position: relative;
  z-index: 99999;
  transform: scale(1.2) translate(-2%, -150px);
  transition: all 0.4s ease-in-out;
}

.tariffCards > div.fourth {
  margin-top: -250px;
  margin-left: 58.5%;
  z-index: 4;
  transition: all 0.4s ease-in-out;
  transform: perspective(800px) rotateY(30deg);
}

.tariffCards > .fourth:hover {
  position: relative;
  z-index: 99999;
  transform: scale(1.2) translate(-68%, -150px);
  transition: all 0.4s ease-in-out;
}

.tariffCards > div.fifth {
  margin-top: -250px;
  margin-left: 78%;
  z-index: 5;
  transition: all 0.4s ease-in-out;
  transform: perspective(800px) rotateY(30deg);
}

.tariffCards > .fifth:hover {
  position: relative;
  z-index: 99999;
  transform: scale(1.2) translate(-133%, -150px);
  transition: all 0.4s ease-in-out;
}





@media (max-width: 768px) {
  .hugo_content {
    visibility: hidden;
    margin-top: 10px;
    height: 0px;
  }
  .introductionContainer {
    display: block;
  }
  .hugo_subtitle {
    font-size: 0.5em !important;
    font-weight: 300;
  }
  .introTextCard {
    margin-top: 55px;
  }
  .introBigText {
    font-size: 0.8em
  }
  .introBigText::after {
    content: " - ";
  }
  .introBigText::before {
    content: " - ";
  }
  
  .introSmallText {
    font-size: 0.5em
  }

  .body {
    height: auto;
    margin-top: 50px;
    display: grid;
    margin-left: 10px;
    margin-right: 10px;
    transition: all 0.4s ease-in-out;
  }

  .tariffCards > .first:hover {
    position: relative;
    z-index: 99999;
    transform: scale(2.5, 1) translate(62%, -150px);
    transition: all 0.4s ease-in-out;
  }
  
  .tariffCards > .second:hover {
    position: relative;
    z-index: 99999;
    transform: scale(2.5, 1) translate(31%, -150px);
    transition: all 0.4s ease-in-out;
  }
  
  .tariffCards > .third:hover {
    position: relative;
    z-index: 99999;
    transform: scale(2.5, 1) translate(0%, -150px);
    transition: all 0.4s ease-in-out;
  }
  
  .tariffCards > .fourth:hover {
    position: relative;
    z-index: 99999;
    transform: scale(2.5, 1) translate(-31%, -150px);
    transition: all 0.4s ease-in-out;
  }
  
  .tariffCards > .fifth:hover {
    position: relative;
    z-index: 99999;
    transform: scale(2.5, 1) translate(-62%, -150px);
    transition: all 0.4s ease-in-out;
    border-radius: 10%;
  }

}

.hugo_subtitle {
  font-size: 1.5em;
  padding-left: 5px;
  padding-bottom: 5px;
  
}

.hugo:hover {
  color: var(--hoverColor)
}

.backColor {
  padding-top: 100px;
  padding-right: 70px;
  padding-left: 70px;
  height: 100vh;
  overflow: hidden;
}







.introMidText {
  font-size: 30px;
  color: #283C59;
  font-family: 'NotoSansKR';
  text-align: center;
  font-weight: bold;
}


.hugo_content {
  margin-top: 70px;
  color: var(--fontColor);
  font-size: 1em;
  text-align: center;
}