.dropdownMenuItem:hover {
  color: #2D5DA6;
  background-color: white !important;
  scale: 1.05;
  transition:  .3s ease;
  z-index: 9999;
}
.dropdownMenu {
  transition:  .3s ease;
  z-index: 9999;
  margin-top: 7px;
}

.dropdownMenuItem.active {
  background-color: white;
}

.originalNavbar {
  color: white;
  background-color: rgba(0, 0, 0, 0);
  display: flex;
  position: sticky;
  top: 0;
  width: 100%;
  z-index: 99999;
  height: 70px;
  transition: all 0.3s ease-in-out;
}
.MuiButtonBase-root:disabled {
  cursor: not-allowed;
  pointer-events: auto;
}
.navbar_container {
  padding-block: 12px;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-inline: 5%;
  margin-inline: 5%;
  z-index: 99999;
}

.scrollNavbar {
  color: black;
  background-color: #2D5DA6;
  display: flex;
  position: sticky;
  top: 0;
  width: 100%;
  z-index: 99999;
  height: 70px;
  transition: all 0.3s ease-in-out;
}

.logoDiv{
  display: flex;
  align-items: center;
}

.logo {
  width: 200px;
  height: 50px;
}