body::-webkit-scrollbar {
  display: none; /* Chrome, Safari, Edge */
}
.container{
  width: 100%;
  height: 100vh; 
}

.emptyContainer{
  width: 100%;
  height: auto;
  background-color: red; 
}

.boxwrap {
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 100%;
  height: auto; 
  overflow: hidden;
  scroll-snap-type: y mandatory;
  overflow-y: auto;
  margin-top: -70px; 
  top:0;
}

.box{
  display: flex;
  justify-content: center;
  width: 100%;
  height: auto;
  color:#ffffff;
  scroll-snap-align: start;
  top:0;
}

.bannerBoldText {
  font-weight: bold;
  z-index: 2;
}

.bannerTextMove{
  animation: fadein 1.5s;
  z-index: 2;
}

.bannerTextCard {
  position: absolute;
  top: 50%;
  left: 10%;
  transform: translate(-50% -60%);
  color: white;
  animation: fadein 1.5s;
  z-index: 2;
  transition: all 0.4s ease-in-out;
}

@keyframes fadein {
  from {
    opacity: 0;
    transform: translate3d(0, 80%, 0);
  }
  to {
    opacity: 1;
    transform: translateZ(0);
  }
}

.bannerTopTextSize {
  color: white;
  font-size: 48px;
  margin: 0;
  font-weight: 400;
  margin-bottom: 0; 
  z-index: 2;
  transition: all 0.4s ease-in-out;
}

.bannerBottomTextSize {
  color: white;
  font-size: 20px;
  margin-top: 50px;
  z-index: 2;
  transition: all 0.4s ease-in-out;
}

