// 혹시 몰라서 백업..
.imgSizs {
  position: relative;
  display: flex; 
  justify-content: center; 
  width: 100%;
  height: 100%;
  top: 0;
  transform: translate(0, -10%);
}

.imgSizs img {
  width: 90%;
  height: 100%;
  border-radius: 0px 0px 100px 100px / 0px 0px 100px 100px;
}

//화면 너비 1200이하일때 글씨크기 조정  transition: all 0.1s ease-in-out;
@media (max-width: 1200px) {
  .bannerTopTextSize {
    font-size: 40px;
  }
}
//화면 너비 768이하일때 글씨크기 조정
@media (max-width: 768px) {
  .bannerTopTextSize {
    font-size: 30px;
  }
  .bannerTextCard {
    top: 60%;
  }
  .bannerBottomTextSize {
    font-size: 16px;
  }
}
//화면 너비 605이하일때 글씨크기 조정
@media (max-width: 605px) {
  .bannerTopTextSize {
    font-size: 28px;
  }
  .bannerTextCard {
    top: 60%;
  }
  .bannerBottomTextSize {
    font-size: 14px;
  }
}

