.textLine{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-bottom: 15px;
  margin-top: 10px;
  transition: all 0.1s ease-in-out;
}

.container {
  width: 100%;
  height: 100vh;
  padding-top: -70px;
}

.slideSPText {
  font-size: 50px;
  color: #2D5DA6;
  margin: 0;
  margin-bottom: 10px;
  font-weight: bold;
  transition: all 0.1s ease-in-out;
}
.slideBigText {
  font-size: 24px;
  color: #2D5DA6;
  margin: 0;
  transition: all 0.1s ease-in-out;
}

.slideBigTextWhitMargin {
  font-size: 28px;
  color: #2D5DA6;
  margin: 0;
  margin-top: 10px;
  margin-bottom: 5px;
  transition: all 0.1s ease-in-out;
  font-weight: 600;
}

.slideSmallText {
  font-size: 18px;
  color: black;
  margin: 0;
  transition: all 0.1s ease-in-out;
}

.custom_slide {
  height: 80%;
  width: 50px;
  /* opacity: 0.5;
  transition: opacity 0.3s; */
  background-color: white;
  border-radius: 80px 80px 80px 80px / 80px 80px 80px 80px;
  box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.5);
}

.custom_slide.swiper-slide-active:before {
  box-shadow: 0px 0px 20px 0px rgba(0, 0, 0, 0.8);
}

.slide_img {
  width: 100%;
  height: 50%;
  display: block;
  margin: 0;
  border-radius: 80px 80px 80px 80px / 80px 80px 0 0;
}

/* .swiper-slide-active {
  opacity: 1 !important;
} */


.content {
  display: flex;
  max-width: 100%;
  max-height: 70%;
}


