#hdContents {
  width: 100%;
  text-align: center;
  color: black;
  display: flex;
  align-items: center;
  height: 100vh;
  background-image: linear-gradient(to top, #0065B1 50%, white 50%);
}

@media (width <=1200px) {
  .textLine{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-bottom: 5px;
  }
  .slideSPText {
    font-size: 40px;
    color: #2D5DA6;
    margin: 0;
    margin-bottom: 5px;
    font-weight: bold;
    transition: all 0.1s ease-in-out;
  }
  .slideBigText {
    font-size: 20px;
    color: #2D5DA6;
    margin: 0;
    transition: all 0.1s ease-in-out;
  }
  .slideBigTextWhitMargin {
    font-size: 20px;
    color: #2D5DA6;
    margin: 0;
    margin-top: 5px;
    transition: all 0.1s ease-in-out;
  }
  .slideSmallText {
    font-size: 16px;
    color: black;
    margin: 0;
    transition: all 0.1s ease-in-out;
  }
}

@media (width <=768px) {
  .textLine{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-bottom: 5px;
    transition: all 0.1s ease-in-out;
  }
  .slideSPText {
    font-size: 35px;
    color: #2D5DA6;
    margin: 0;
    margin-bottom: 5px;
    font-weight: bold;
    transition: all 0.1s ease-in-out;
  }
  .slideBigText {
    font-size: 20px;
    color: #2D5DA6;
    margin: 0;
    transition: all 0.1s ease-in-out;
  }
  .slideBigTextWhitMargin {
    font-size: 20px;
    color: #2D5DA6;
    margin: 0;
    margin-top: 5px;
    transition: all 0.1s ease-in-out;
  }
  .slideSmallText {
    font-size: 14px;
    color: black;
    margin: 0;
    transition: all 0.1s ease-in-out;
  }
}

