.bigImg {
  width: 100%;
  height: 700px;
  top:0;
  margin-top: -70px;
  position: relative;
}

.imgSizs {
  position: relative;
  width: 100%;
  height: 100%;
  top: 0;
}

.titleTextCard {
  position: absolute;
  width: 100%;
  top: 40%;
  color: white;
  animation: fadein 1.5s;
  z-index: 2;
  transition: all 0.4s ease-in-out;
}

.smallText {
  font-size: 24px;
  margin: 0;
  text-align: center;
}

.bigText {
  font-size: 60px;
  margin: 0;
  text-align: center;
}